      //   return
      // }
      // this.$toast.loading({
      //   message: '...',
      //   forbidClick: true,
      //   duration: 0,
      //   loadingType: 'spinner'
      // })
      // let arr = this.dataobj.product[item]
      // // ;
      // // 
      // arr.forEach(v => {
      //   let data = utils.queryString({
      //     appkey: this.appkey,
      //     pid: v.pid,
      //     tokenString,
      //     count: v.count,
      //     rule: v.rule
      //   })
      //   let axios = this.axios({
      //     method: 'POST',
      //     url: '/addShopcart',
      //     //getparams, post,data
      //     data
      //   })
      //     .then(result => {
      //       this.$toast.clear()
      //       if (res.code === 3000) {
      //         let sid = res.sid
      //         this.sidarrs.push(sid)
      //       }
      //       this.$toast(res.msg)
      //     })
      //     .catch(err => {
      //       this.$toast.clear()
      //       return
      //     })
      //   all.push(axios)
      // })
      // // sid  ,
      // // allaxiossid
      // Promise.all(all)
      //   .then(() => {
      //     let obj = JSON.stringify(this.sidarrs)
      //     let ischeaks = true
      //     // 
      //     console.log(obj)
      //     this.$router.push({ name: 'Tijiao', query: { obj, ischeaks } })
      //     // bug  
      //     //  sid
      //     // 
      //     //  
      //   })
      //   .catch(shibai => {})
    }
  }
}
</script>

