<template>
  <div class="submitorder">
    <div class="submitordertitle">
      <van-nav-bar
        title=""
        :left-text="''"
        @click-left="back"
        fixed
      ></van-nav-bar>
    </div>

    <div class="dizhi">
      <div class="van-addressadd">
        <div class="select-address" @click="ischeak">
          <div></div>
          <van-icon name="arrow" />
        </div>
        <div class="info">
          <div class="infoname">{{ selectaddress.name }}</div>
          <div class="infophone">{{ selectaddress.tel }}</div>
        </div>
        <div class="address">
          {{ selectaddress.city }}{{ selectaddress.county
          }}{{ selectaddress.addressDetail }}
        </div>
      </div>

      <van-popup v-model="show">
        <van-address-edit
          show-postal
          show-set-default
          :area-columns-placeholder="['', '', '']"
          :area-list="areaList"
          :address-info="addressInfo"
          @save="newAddress"
        />
      </van-popup>

      <van-popup v-model="setshow">
        <van-address-edit
          show-postal
          show-delete
          show-set-default
          :area-columns-placeholder="['', '', '']"
          :area-list="areaList"
          :address-info="addressInfo"
          @save="setAddress"
          @delete="deleteAddress"
        />
      </van-popup>

      <van-popup v-model="cheak" position="bottom">
        <van-address-list
          v-model="chosenAddressId"
          :list="list"
          default-tag-text=""
          @add="onadd"
          @edit="onedit"
          @select="onselect"
        />
      </van-popup>
    </div>
    <div class="lists-item">
      <h3></h3>
      <van-empty description="" v-if="isshow">
        <van-button round type="danger" class="bottom-button" @click="goshop"
          ></van-button
        >
      </van-empty>
      <van-swipe-cell v-for="(value, index) in resultdata" :key="index">
        <van-cell-group>
          <div class="cell-box">
            <div class="cell-img">
              <img class="auto-img" :src="value.small_img" alt />
            </div>
            <div class="cell-info">
              <div class="info-top">
                {{ value.name }}
                <span>{{ value.rule }}</span>
              </div>
              <div class="info-center">{{ value.enname }}</div>
              <div class="info-bottom">
                <div class="price">{{ value.price }}</div>
                <div>x {{ value.count }}</div>
              </div>
            </div>
          </div>
        </van-cell-group>
      </van-swipe-cell>
    </div>
    <div class="sum">
      <div class="num"> {{ resultdata.length }} </div>
      <div class="sums">{{ sum }}</div>
    </div>
    <div class="btn">
      <van-button type="primary" @click="uporder"></van-button>
    </div>
  </div>
</template>

<script>
import { Dialog } from 'vant'
import { areaList } from '@vant/area-data'
import { getToken } from '@/utils/storage'
import {
  findAddressAPI,
  commitShopcartAPI,
  editAddressAPI,
  deleteAddressAPI,
  addAddressAPI,
  payAPI
} from '@/api/user'
import '../assets/less/submitorder.less'

export default {
  data () {
    return {
      // 
      isshow: false,
      show: false,
      cheak: false,
      setshow: false,
      // 
      sids: '',
      // 
      resultdata: {},
      // 
      sum: 0,
      areaList,
      // 
      addressInfo: {
        id: '',
        name: '',
        tel: '',
        province: '',
        city: '',
        county: '',
        addressDetail: '',
        areaCode: '',
        postalCode: '',
        isDefault: false
      },
      chosenAddressId: '1',
      list: [],
      // 
      selectaddress: {},
      // 
      address: '',
      // 
      ischeaks: false
    }
  },
  created () {
    // 
    if (!getToken()) return this.$router.push({ name: 'Login' })
    this.sids = this.$route.query.obj
    this.ischeaks = this.$route.query.ischeaks
    this.each()
    this.loading()
  },
  beforeRouteLeave (to, from, next) {
    next(true)
    if (this.ischeaks) {
      this.remove(this.sids)
    }
  },
  methods: {
    // 
    loading () {
      const tokenString = getToken()
      if (this.sids) {
        console.log('this.sids', this.sids)
        commitShopcartAPI({
          appkey: this.$store.state.appkey,
          tokenString,
          sids: this.sids
        }).then(res => {
          console.log(res)
          if (res.code === 50000) {
            this.resultdata = res.result
            this.resultdata.forEach(v => {
              this.sum += v.count * v.price
            })
          }
          if (res.code === 50001) {
            this.resultdata = {}
            this.isshow = true
          } else {
            this.isshow = false
          }
        })
      }
    },
    // 
    back () {
      this.$router.go(-1)
    },
    // 
    goshop () {
      this.$router.push({ name: 'Dingdan' })
    },
    // 
    newAddress (address) {
      const content = Object.assign({}, address)
      delete content.id
      delete content.country
      content.isDefault = Number(content.isDefault)
      content.appkey = this.$store.state.appkey
      content.tokenString = getToken()
      addAddressAPI(content).then(res => {
        this.each()
        this.show = false
      })
    },
    // 
    deleteAddress (address) {
      if (!address.aid) {
        return
      }
      const tokenString = getToken()
      deleteAddressAPI({
        appkey: this.$store.state.appkey,
        tokenString,
        aid: address.aid
      }).then(res => {
        this.addressInfo = {
          id: '',
          name: '',
          tel: '',
          province: '',
          city: '',
          county: '',
          addressDetail: '',
          areaCode: '',
          postalCode: '',
          isDefault: false
        }
        this.each()
        this.show = false
        this.setshow = false
      })
    },
    // 
    async each () {
      // token
      const tokenString = getToken()
      const res = await findAddressAPI({
        appkey: this.$store.state.appkey,
        tokenString
      })
      if (res) {
        res.result.forEach(v => {
          if (v.isDefault) {
            this.selectaddress = v
            this.address = v.city + v.county + v.addressDetail
          }
        })
        this.list = res.result
        console.log(this.list)
      }
    },
    // 
    onedit (item) {
      this.setshow = !this.setshow
      this.addressInfo = item
    },
    // 
    setAddress (address) {
      const content = Object.assign({}, address)
      const tokenString = getToken()

      delete content.id
      delete content.country
      content.isDefault = Number(content.isDefault)
      content.appkey = this.$store.state.appkey
      content.tokenString = tokenString
      content.aid = this.addressInfo.aid
      editAddressAPI(content).then(res => {
        console.log(res)
        this.each()
      })
    },
    // 
    onselect (item) {
      this.selectaddress = item
      this.cheak = false
    },
    ischeak () {
      // findAddress
      this.cheak = !this.cheak
    },
    // 
    onadd () {
      this.show = !this.show
      //  
      this.addressInfo = {
        id: '',
        name: '',
        tel: '',
        province: '',
        city: '',
        county: '',
        addressDetail: '',
        areaCode: '',
        postalCode: '',
        isDefault: false
      }
    },
    // 
    uporder () {
      if (!this.selectaddress.aid) {
        Dialog.alert({
          message: ''
        }).then(() => {})
        return
      }
      const tokenString = getToken()
      payAPI({
        appkey: this.$store.state.appkey,
        tokenString,
        sids: this.sids,
        phone: this.selectaddress.tel,
        address: this.address,
        receiver: this.selectaddress.name
      }).then(res => {
        this.sids = []
        this.sids = JSON.stringify(this.sids)
        if (res.code === 60001) {
          return
        }
        if (res.code === 60000) {
          this.loading()
          this.sum = 0
          this.$toast(res.msg)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped></style>
