<template>
  <div class="shopcar">
    <van-nav-bar
      title=""
      :right-text="isshow ? '' : isEdit ? '' : ''"
      fixed
      @click-right="compile"
    ></van-nav-bar>
    <div class="shopcar-body">
      <van-empty description="" v-if="isshow">
        <van-button round type="danger" class="bottom-button" @click="gocaidan"
          ></van-button
        >
      </van-empty>
      <van-swipe-cell
        :before-close="beforeClose"
        v-for="(value, index) in resultdata"
        :key="index"
      >
        <van-cell-group>
          <div class="cell-box">
            <div class="cheak">
              <van-checkbox
                v-model="value.ischeak"
                @change="checka(value)"
              ></van-checkbox>
            </div>
            <div class="cell-img" @click="godatail(value)">
              <img class="auto-img" :src="value.small_img" alt />
            </div>
            <div class="cell-info">
              <div class="info-top">
                {{ value.name }}
                <span>{{ value.rule }}</span>
              </div>
              <div class="info-center">{{ value.enname }}</div>
              <div class="info-bottom">
                <div class="price">{{ value.price }}</div>
                <div>
                  <van-stepper
                    v-model="value.count"
                    theme="round"
                    @change="changes(value)"
                    button-size="22"
                    disable-input
                  />
                </div>
              </div>
            </div>
          </div>
        </van-cell-group>

        <template #right>
          <van-button
            square
            color="#0b37be"
            @click="letthis(value)"
            text=""
          />
        </template>
      </van-swipe-cell>
    </div>
    <div class="shopca-bottom" v-if="ishide">
      <van-submit-bar
        :price="price"
        :button-text="isEdit ? '' : ''"
        @submit="onSubmit"
      >
        <van-checkbox v-model="isAll" @click="allCheck"></van-checkbox>
      </van-submit-bar>
    </div>
  </div>
</template>

<script>
import { getToken } from '@/utils/storage'
import '../../assets/less/Shoucar.less'
import {
  findAllShopcartAPI,
  removeShopcartAPI,
  modifyShopcartCountAPI
} from '@/api/user'
import { Dialog } from 'vant'
export default {
  data () {
    return {
      isshow: true,
      ishide: true,
      isEdit: false,
      isAll: false,
      resultdata: [],
      price: 0,
      sid: []
    }
  },
  created () {
    this.eachshop()
    if (this.resultdata.length) {
      this.isshow = false
      this.ishide = true
    } else {
      this.isshow = true
      this.ishide = false
    }
  },
  updated () {
    if (this.resultdata.length) {
      this.isshow = false
      this.ishide = true
    } else {
      this.isshow = true
      this.ishide = false
    }
  },
  methods: {
    // 
    godatail (value) {
      this.$router.push({ name: 'Detail', query: { pid: value.pid } })
    },
    //   
    async eachshop () {
      const tokenString = getToken()
      if (!tokenString) return
      const res = await findAllShopcartAPI({
        appkey: this.$store.state.appkey,
        tokenString
      })
      if (res.code === 5000) {
        for (let i = 0; i < res.result.length; i++) {
          res.result[i].ischeak = false
        }

        this.resultdata = res.result
      }
    },
    // 
    compile () {
      this.isEdit = !this.isEdit
    },
    // id
    letthis (value) {
      this.sid = []
      this.sid.push(value.sid)
      this.sid = JSON.stringify(this.sid)
    },
    // 
    beforeClose ({ position, instance }) {
      console.log(position, instance)
      switch (position) {
        case 'left':
        case 'cell':
        case 'outside':
          instance.close()
          break
        case 'right':
          Dialog.confirm({
            message: ''
          })
            .then(() => {
              instance.close()
              this.remove()
              //  
            })
            .catch(ree => {
              // 
            })
          break
      }
    },
    // 
    onSubmit () {
      // 
      this.sid = []
      this.resultdata.forEach(v => {
        if (v.ischeak) {
          this.sid.push(v.sid)
        }
      })
      if (this.sid.length === 0) {
        Dialog.alert({
          message: ''
        }).then(() => {
          // on close
        })
        return
      }

      this.sid = JSON.stringify(this.sid)
      const obj = this.sid
      const ischeaks = false
      // 
      if (this.isEdit) {
        this.remove()
        //  
      } else {
        this.$router.push({ name: 'Tijiao', query: { obj, ischeaks } })
      }
    },
    // 
    checka (value) {
      const cheak = this.resultdata.every(v => {
        return v.ischeak === true
      })
      this.isAll = cheak
      this.sum()
    },
    // 
    allCheck () {
      const isAll = this.isAll
      this.resultdata.forEach(v => {
        v.ischeak = isAll
      })
      this.sum()
    },
    // 
    sum () {
      this.price = 0
      this.resultdata.forEach(v => {
        if (v.ischeak) {
          this.price += v.count * v.price * 100
        }
      })
    },
    // 
    changes (value) {
      this.resetshop(value)
      this.sum()
    },
    // 
    async resetshop (value) {
      const tokenString = getToken()
      if (!tokenString) return
      // //
      const data = {
        appkey: this.$store.state.appkey,
        tokenString,
        count: value.count,
        sid: value.sid
      }
      console.log(data)
      const res = await modifyShopcartCountAPI(data)
      // 
      if (res.code === 6000) {
        this.$toast(res.msg)
      }
    },

    // 
    async remove () {
      const tokenString = getToken()
      if (!tokenString) return
      const data = {
        appkey: this.$store.state.appkey,
        tokenString,
        sids: this.sid
      }
      // console.log(data)
      const res = await removeShopcartAPI(data)
      // 
      if (res.code === 7000) {
        this.$toast(res.msg)
        this.eachshop()
        this.sum()
      }
      console.log(res)
    },
    // 
    gocaidan () {
      this.$router.push({ name: 'Caidan' })
    }
  }
}
</script>

<style lang="less" scoped></style>
