<template>
  <div class="Login">
    <van-nav-bar left-arrow @click-right="back" class="login-top">
      <template #left>
        <div class="logo iconfont">&#xe50c;</div>
        <div class="logotext">Luckin Coffee</div>
      </template>
      <template #right>
        <div class="back" @click="gomain"></div>
      </template>
    </van-nav-bar>
    <div class="login-text">
      <div class="welcome">!</div>
      <div class="english">Please login to your accounts</div>
    </div>
    <div class="login-input">
      <van-form @submit="onSubmit">
        <!--  -->
        <van-field
          v-model="phone"
          name="phone"
          label=""
          placeholder=""
          :rules="UserRules"
        />
        <van-field
          :type="isPassword ? 'password' : 'text'"
          v-model="password"
          name="password"
          label=""
          placeholder=""
          :rules="PasswordRules"
          :right-icon="isPassword ? 'closed-eye' : 'browsing-history'"
          @click-right-icon="isPassword = !isPassword"
        />

        <div class="pass clearfix">
          <div class="fr" @click="forget">?</div>
        </div>
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            ></van-button
          >
          <van-button
            type="default"
            class="register"
            @click.prevent="show = true"
            ></van-button
          >
        </div>
      </van-form>
      <!--  -->
      <van-popup
        class="toregister"
        v-model="show"
        position="bottom"
        round
        overlay-class="mask"
        :style="{ height: '30%' }"
      >
        <van-form @submit="Reaisteruser">
          <div class="logintrue">
            <div class="comeback">
              <van-icon name="cross" @click="show = false" />
            </div>
            <div class="nametop"></div>
          </div>
          <van-field
            name="phone"
            label=""
            placeholder=" (+86)"
            :rules="UserRules"
            v-model="userRegister.phone"
          />
          <van-field
            v-model="userRegister.nickName"
            name="nickName"
            label=""
            placeholder=""
            :rules="usernamerules"
          />
          <van-field
            name="password"
            :rules="PasswordRules"
            label=""
            v-model="userRegister.password"
            @click-right-icon="isRegister = !isRegister"
            :type="isRegister ? 'password' : 'text'"
            placeholder=""
            :right-icon="isRegister ? 'closed-eye' : 'browsing-history'"
          />
          <div style="margin: 16px">
            <van-button type="default" class="register"></van-button>
          </div>
        </van-form>
      </van-popup>
    </div>
  </div>
</template>

<script>
import '../assets/less/login.less'
import { loginAPI, registerAPI } from '@/api/user.js'
import { setToken } from '@/utils/storage'

export default {
  name: 'Login',
  data () {
    //
    return {
      // 
      phone: '',
      password: '',
      // 
      // 
      UserRules: [
        {
          required: true,
          message: '',
          pattern: /^1[3-9]\d{9}$/
        }
      ],
      PasswordRules: [
        {
          required: true,
          message: '',
          pattern: /^[A-Za-z]\w{5,15}$/
        }
      ],
      usernamerules: [
        {
          required: true,
          message: '',
          pattern: /^[\w\u4e00-\u9fa5]{1,10}$/
        }
      ],
      userRegister: {
        phone: '',
        nickName: '',
        password: ''
      },
      isPassword: true,
      isRegister: true,
      show: false
    }
  },
  methods: {
    // 
    onSubmit (values) {
      loginAPI(values)
        .then(res => {
          this.$toast(res.msg)
          if (res.code === 200) {
            setToken(res.token) //
            this.$router.push({ name: 'My' })
          }
        })
        .catch(err => {
          console.log('error', err)
          // this.$toast(err.msg)
        })
    },
    // 
    gomain () {
      this.$router.push({ name: 'Main' })
    },
    // 
    back () {},
    // 
    forget () {
      this.$router.push({ name: 'Forgetpassword' })
    },
    Reaisteruser (values) {
      registerAPI(values)
        .then(res => {
          this.$toast(res.msg)
        })
        .catch(err => {
          console.log('err', err) // this.$toast(err.msg)
        })
    }
  }
}
</script>

